<template>
  <div class="layout">
    <topHeader></topHeader>
    <layout></layout>
    <router-view></router-view>
    <div class="out"><el-button type="danger" @click="out">退出登录</el-button></div>
  </div>
</template>

<script>
  import layout from './components/side-bar'
  import topHeader from './components/top-header'
  export default {
    name: "index",
    components: {
      layout,
      topHeader
    },
    methods:{
      out(){
        this.$axios.post('out').then(res=>{
          console.log(res.data)
          if(res.code==200){
            this.$message({
              type: 'success',
              message: '退出成功!'
            });
            setTimeout(()=>{
              this.$router.push("/login")
            },500)
          }else{
            this.$message({
              type: 'warning',
              message: res.msg
            });
          }
        })
      }
    }
  }
</script>

<style scoped>
 .out{
   position: fixed;
   right: 20px;
   top: 2px;
   z-index: 1001;
 }
.layout{
  padding-left: 240px;
  padding-top: 55px;
}
</style>



