import Vue from 'vue'
import Router from 'vue-router'
import layout from '@/views/layout/index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/',
      redirect: '/login'
    },
    {
      path: '/login',
      name: '登录',
      component: ()=>import("@/views/login/login")
    },
    {

      path: '/header',
      name: '首页',
      component: layout,
      children:[
        {
          path: '/crmnews',
          name: '新闻列表',
          component: ()=>import('@/views/news/list')
        },
        {
          path: '/crmnews/add',
          name: '新闻添加',
          component: ()=>import('@/views/news/add')
        },
        {
          path: '/crmnews/update',
          name: '新闻编辑',
          component: ()=>import('@/views/news/add')
        },
        {
          path: '/crmslideShows/add',
          name: '轮播图添加',
          component: ()=>import('@/views/slideShows/add')
        },
        {
          path: '/crmslideShows/update',
          name: '轮播图编辑',
          component: ()=>import('@/views/slideShows/add')
        },
        {
          path: '/crmslideShows',
          name: '轮播图列表',
          component: ()=>import('@/views/slideShows/list')
        },
        {
          path: '/crmcategory',
          name: '分类列表',
          component: ()=>import('@/views/fenlei/list')
        },
        {
          path: '/crmcategory/add',
          name: '分类添加',
          component: ()=>import('@/views/fenlei/add')
        },
        {
          path: '/crmcategory/update',
          name: '分类编辑',
          component: ()=>import('@/views/fenlei/add')
        },
        {
          path: '/crmusercategory',
          name: '用户分类列表',
          component: ()=>import('@/views/userfenlei/list')
        },
        {
          path: '/crmusercategory/add',
          name: '用户分类添加',
          component: ()=>import('@/views/userfenlei/add')
        },
        {
          path: '/crmusercategory/update',
          name: '用户分类编辑',
          component: ()=>import('@/views/userfenlei/add')
        },
        {
          path: '/crmusers',
          name: '用户列表',
          component: ()=>import('@/views/users/list')
        },
        {
          path: '/crmusers/add',
          name: '用户添加',
          component: ()=>import('@/views/users/add')
        },
        {
          path: '/crmaudits',
          name: '总结列表',
          component: ()=>import('@/views/audits/list')
        },
        {
          path: '/crmaudits/think',
          name: '思想汇报',
          component: ()=>import('@/views/audits/think')
        },
        {
          path: '/crmaudits/summary',
          name: '个人总结',
          component: ()=>import('@/views/audits/summary')
        },
        {
          path: '/crminvits',
          name: '帖子列表',
          component: ()=>import('@/views/invits/list')
        },
        {
          path: '/crmdemocratic',
          name: '评议列表',
          component: ()=>import('@/views/democratic/list')
        },
        {
          path: '/crmdemocratic/add',
          name: '发起评议',
          component: ()=>import('@/views/democratic/add')
        },
        {
          path: '/register',
          name: '注册',
          component: ()=>import("@/views/login/register")
        },
        {
          path: '/changePwd',
          name: '修改密码',
          component: ()=>import("@/views/login/changePwd")
        }
      ]
    }
  ]
})



