<template>
  <div class="header">
    <el-breadcrumb separator="/">
      <el-breadcrumb-item v-for="(item,index) in routerList" :to="item.path" :key="index">{{item.name}}</el-breadcrumb-item>
    </el-breadcrumb>
  </div>
</template>

<script>
  export default {
    name: "top-header",
    data(){
      return {
        routerList:[]
      }
    },
    methods:{
      getlist(){
        //matched 是一个数组，它所有的值进来，filter接受的是一个布尔值，如果里面有个这个那么值，那么就会被保留下来，用来过滤的
        this.routerList = this.$route.matched.filter((item=>item.name))
      }
    },
    watch:{
      $route(){
        this.getlist()
      }
    },
    created(){
      this.getlist()
    }
  }
</script>

<style scoped>
.header{
  position: fixed;
  top: 0;
  left: 230px;
  right: 0;
  border-bottom:1px solid #cd0200;
  padding: 15px;
  z-index: 1000;
  color: white;
  font-weight: 500;
}
</style>



